@import url('https://fonts.googleapis.com/css2?family=Public+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

html {
  width: 100%;
  height: 100vh;
  //background-color: #F3FBFF;
  background-color: #ffffff;
  opacity: 1;
  background-image:  linear-gradient(#fafafa 1px, transparent 1px), linear-gradient(to right, #fafafa 1px, #ffffff 1px);
  background-size: 20px 20px;
  font-family: 'Public Sans', sans-serif;
  color: #4B8DD6;
  display: flex;
  flex-direction: column;
  align-items: center;
}

body {
  width: 100%;
}

.Tab {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 100px;
  width: 100%;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

}

.nav {
  width: 100%;
  padding-top: 50px;
  display: flex;
  flex-direction: row;
  max-width: 1200px;
  img {

    width: 100px;
  }
}

@media only screen and (max-device-width: 480px){
  .nav {
    padding-left: 10%;
    padding-bottom: 20%;
  }
  .buttons {
    flex-direction: column;
    button {
      padding-left: 120px !important;
      padding-right: 120px  !important;

    }
  }
  .section {
    max-width: none !important;

  }
  .hero {

    button {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      gap: 0px;
      width: 100% !important;
      
      img{
        width: 10px;
        display: none;
      }
    }
    &__heading-main {
      font-size: 40px !important;
    }
    max-width: none !important;
    flex-direction: column-reverse;
    justify-content: flex-end;
    align-items: center;
    text-align: center;
    &__image {
      padding-left: 0px !important;
      padding-bottom: 20px !important;
    }
  }
  .section {
    max-width: 90% !important;
  
  &__center {
    max-width: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &-content {
      text-align: center;
      padding-bottom: 30px;
    }
  }

  &__left {
    display: flex;
    flex-direction: column !important;
    text-align: center;
    justify-content: center;
    align-items: center;
    gap: 50px;
  }
}

}

.hero {

  max-width: 1200px;

  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 100px;
  padding-left: 50px;
  padding-right: 50px;
  &__heading {
    &-main {
      font-size: 40px;
      font-weight: 700;
    }

    &-sub {
      font-size: 20px;
      
    }

    &-buttons{
      padding-bottom: 30px;
      font-size: 20px;
      padding-top: 80px;
      font-weight: 700;


    }
    button{
      margin-top: 10px;
      display: flex;
      flex-direction: row;
      border-radius: 7px;
      padding-left: 15px;
      width: 250px;
      gap: 20px;
      border: 1px #4B8DD6 solid;

      padding-top: 10px;
      padding-bottom: 10px;

      img{
        width: 20px;
        fill: #4B8DD6;
      }
    }

  }
  &__image {
    padding-left: 100px;
    width: 600px;
  }
}

.section {
  max-width: 1200px;
  
  &-main {
    font-size: 40px;
    font-weight: 600;
  }
  
  &-sub {
    font-size: 20px;
    color: #A5A5A5;
  }


  &__center {
    max-width: 600px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &-content {
      text-align: center;
      padding-bottom: 30px;
    }
    
    &__mentor {
      max-width: 900px;
    }
  }

  &__left {

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 50px;
    img {
      width: 600px;
    } 
  }

}
.tabs {
  p {
    font-size: 20px;
    font-weight: 600;
    text-align: center;
  }
  .buttons {
    display: flex;
    padding: 20px;
    padding-bottom: 100px;
    button {
      border: 1px #4B8DD6 solid;
      border-radius: 7px;
      padding: 75px;
      padding-top: 10px;
      padding-bottom: 10px;
      margin: 20px;
    }
  }

}

.block {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-top: 100px;
  padding-bottom: 100px;

  &-blue {
    background-color: #F3FBFF;
  }

  &-white {
    background-color: #ffffff;
    opacity: 1;
    background-image:  linear-gradient(#fafafa 1px, transparent 1px), linear-gradient(to right, #fafafa 1px, #ffffff 1px);
    background-size: 20px 20px;
    border: #fafafa 1px solid;
  }

}

.footer {
  background: #F3FBFF;
  width: 100%;
  padding-bottom: 40px;
  h1 {
    padding-top: 60px;
    text-align: center;
    font-size: 30px;
    font-weight: 600;
  }
  h2 {
    text-align: center;
    padding-bottom: 20px;
    font-size: 20px;
    font-weight: 300;

  }
  h3 {
    font-weight: 300;
    text-align: center;
    padding-bottom: 20px;
    font-size: 20px;
  }
}
